@font-face {
  font-family: "fontello";
  src: url("/app/font/");
  src: url("/app/font/#iefix") format("embedded-opentype"),
    url("/app/font/fontello.woff2?38685115") format("woff2"),
    url("/app/font/fontello.woff?38685115") format("woff"),
    url("/app/font/fontello.ttf?38685115") format("truetype"),
    url("/app/font/fontello.svg?38685115#fontello") format("svg");
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
/*
  @media screen and (-webkit-min-device-pixel-ratio:0) {
    @font-face {
      font-family: 'fontello';
      src: url('../font/fontello.svg?38685115#fontello') format('svg');
    }
  }
  */

[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "fontello";
  font-style: normal;
  font-weight: normal;
  speak: never;

  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: 0.2em;
  text-align: center;
  /* opacity: .8; */

  /* For safety - reset parent styles, that can break glyph codes*/
  font-variant: normal;
  text-transform: none;

  /* fix buttons height, for twitter bootstrap */
  line-height: 1em;

  /* Animation center compensation - margins should be symmetric */
  /* remove if not needed */
  margin-left: 0.2em;

  /* you can be more comfortable with increased icons size */
  /* font-size: 120%; */

  /* Font smoothing. That was taken from TWBS */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /* Uncomment for 3D effect */
  /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
}

.icon-right-circle:before {
  content: "\e4a2";
} /* '' */
.icon-left-open:before {
  content: "\e75d";
} /* '' */
.icon-angle-left:before {
  content: "\f104";
} /* '' */
.icon-circle-empty:before {
  content: "\f10c";
} /* '' */
.icon-circle:before {
  content: "\f111";
} /* '' */
.icon-angle-circled-left:before {
  content: "\f137";
} /* '' */
.icon-ellipsis-vert:before {
  content: "\f142";
} /* '' */
.icon-dot-circled:before {
  content: "\f192";
} /* '' */
.icon-circle-thin:before {
  content: "\f1db";
} /* '' */
