body {
  margin: 0;
  padding: 0;
  font-family: var(--ff-rubik);
  font-size: 1rem;
}

img {
  max-width: 100%;
}

.bg-1 {
  background: linear-gradient(to bottom, #c740ff 0%, #9c3cff 80%, #9438ff 100%);
  width: 500px;
  height: 700px;
  border-radius: 0 0 253px 240px;
  position: fixed;
  top: 0;
  right: 50%;
  margin-right: 20.8333333333%;
  z-index: -1;
  animation: move 10s linear infinite;
  transform: translateY(-30px);
}

.bg-2 {
  background: #faf5fb;
  width: 500px;
  height: 681px;
  border-radius: 239px 244px 0 0;
  position: fixed;
  bottom: 0;
  left: 50%;
  margin-left: 27.43%;
  z-index: -1;
  animation: move 8s ease infinite;
  transform: translateY(30px);
}

@keyframes move {
  50% {
    transform: translateY(0);
  }
}
.container {
  width: 80%;
  max-width: 1440px;
  margin: 0 auto;
  min-height: 100vh;
  padding-top: 148px;
}

.row {
  display: flex;
  justify-content: space-between;
}

.left {
  width: 50%;
  padding-right: 9.5486111111%;
}

.mobile-wrap {
  width: 247px;
  height: 507px;
  background: var(--white);
  border-radius: 30px;
  padding: 10px;
  margin-left: auto;
  box-shadow: 0 27px 45px -5px #0000001f;
  position: relative;
}

.mobile {
  background: #f5f3f8;
  height: 100%;
  border-radius: 20px;
  overflow: hidden;
}
.mobile:before {
  content: "";
  width: 130px;
  height: 30px;
  position: absolute;
  background: #fff;
  top: 0;
  left: 50%;
  margin-left: -65px;
  border-radius: 50px;
  z-index: 1;
}

.user-wrap {
  background: linear-gradient(to left, var(--lightMagenta) 0%, var(--lightViolet) 100%);
  height: 67px;
  display: flex;
  justify-content: space-between;
  padding: 19px 15px 0 9px;
  align-items: center;
  border-radius: 0 0 5px 5px;
  box-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.185);
}
.user-wrap .icon-angle-left {
  transform: rotateY(-180deg);
  width: 4px;
  height: 14px;
}
.user-wrap button {
  border: 0;
  background: transparent;
  color: #fff;
  outline: none;
  cursor: pointer;
  margin-right: 2px;
}
.user-wrap .avatar {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  border: 1px solid #fff;
  margin-right: 8px;
}

.user {
  flex-grow: 1;
  line-height: 0.4;
}
.user .name {
  font-size: 0.75rem;
  color: #fff;
  margin-bottom: 0.4166666667em;
}
.user .status {
  font-size: 0.5rem;
  color: var(--paleViolet);
}

.convo-wrap {
  height: 364px;
  overflow: auto;
  padding: 11px 7px 0;
  font-size: 0.5rem;
}
.convo-wrap::-webkit-scrollbar {
  width: 5px;
}
.convo-wrap::-webkit-scrollbar-track {
  background: #f1f1f1;
}
.convo-wrap::-webkit-scrollbar-thumb {
  background: #e4e3e3;
}
.convo-wrap::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.convo-wrap .received + .sent,
.convo-wrap .sent + .received {
  margin-top: 14px;
}
.convo-wrap .received,
.convo-wrap .sent {
  display: flex;
  margin-bottom: 8px;
}
.convo-wrap .received:last-child,
.convo-wrap .sent:last-child {
  margin-bottom: 0;
}
.convo-wrap .received p,
.convo-wrap .sent p {
  display: inline-block;
  max-width: 128px;
  overflow-wrap: break-word;
  word-wrap: break-word;
  hyphens: auto;
  padding: 6px 8px;
  line-height: 11px;
}
.convo-wrap .received p {
  background: #ede5f4;
  color: var(--moderateViolet);
  margin-right: auto;
  border-radius: 10px 10px 10px 4px;
}
.convo-wrap .sent p {
  background: #ffffff;
  color: var(--desaturatedDarkViolet);
  margin-left: auto;
  border-radius: 10px 10px 4px 10px;
}
.convo-wrap .file-group {
  display: flex;
}
.convo-wrap .file-group.received {
  justify-content: flex-start;
}
.convo-wrap .file-group.received img + img {
  margin-right: 0.5rem;
}
.convo-wrap .file-group.sent {
  justify-content: flex-end;
}
.convo-wrap .file-group.sent img + img {
  margin-left: 0.5rem;
}
.convo-wrap .file-group img {
  width: 2.5rem;
  border-radius: 10px;
}
.convo-wrap .radio {
  width: 10rem;
  display: inline-block;
  padding: 7px 10px;
  background: linear-gradient(to right, var(--lightMagenta) 0%, var(--lightViolet) 100%);
  border-radius: 10px 10px 10px 4px;
}
.convo-wrap .radio label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #ffa9ff;
  position: relative;
  padding-left: 4px;
}
.convo-wrap .radio label input {
  margin-right: 0.5rem;
  -webkit-appearance: none;
  padding-left: 5px;
}
.convo-wrap .radio label input:checked ~ .icon-circle-thin:before {
  content: "\f111";
  font-family: "fontello";
}
.convo-wrap .radio strong {
  margin-left: auto;
  font-size: 0.75rem;
  color: #fff;
}
.convo-wrap .radio i {
  position: absolute;
  top: -1px;
  left: -6px;
  font-size: 13px;
  color: #ec63ff;
}

.keypad-wrap {
  padding: 10px;
}
.keypad-wrap .input-group {
  position: relative;
}
.keypad-wrap input[type=text] {
  width: 100%;
  height: 35px;
  border: 0;
  border-radius: 50px;
  padding: 0 45px 0 20px;
  outline: 0;
  font-size: 0.53125rem;
}
.keypad-wrap input[type=text]::placeholder {
  color: var(--grayishBlue);
  opacity: 1;
}
.keypad-wrap input[type=text]:-ms-input-placeholder {
  color: var(--grayishBlue);
}
.keypad-wrap input[type=text]::-ms-input-placeholder {
  color: var(--grayishBlue);
}
.keypad-wrap .btn-submit {
  background: transparent;
  outline: none;
  border: 0;
  position: absolute;
  top: 50%;
  right: -1px;
  transform: translateY(-50%);
  font-size: 25px;
  color: var(--veryDarkDesaturatedViolet);
  display: flex;
}
.keypad-wrap .btn-submit .icon-right-circle {
  width: 25px;
  height: 25px;
}

.right {
  width: 50%;
  padding: 160px 9% 160px 1.3888888889%;
}
.right h1 {
  font-size: 2.5rem;
  font-weight: 500;
  color: var(--veryDarkDesaturatedViolet);
  margin-bottom: 0.575em;
}
.right p {
  color: var(--darkGrayishViolet);
  line-height: 1.7;
}

@media (max-width: 768px) {
  .bg-1 {
    top: -190px;
    margin-right: 0;
  }

  .bg-2 {
    margin-left: 0;
    bottom: -170px;
  }

  .container {
    padding-top: 63px;
  }

  .row {
    flex-direction: column;
    justify-content: flex-start;
  }
  .row > .left,
.row .right {
    width: 100%;
  }
  .row > .left {
    padding: 0;
  }
  .row > .right {
    padding: 63px 0;
    text-align: center;
  }

  .mobile-wrap {
    margin: 0 auto;
  }
}


